import React from 'react';
import './login.scss';
import { Link } from 'react-router-dom';
const Login = () => {
  return (
    
    <div className='login'>
      <div className="loginWrapper">
        <div className="registerLeft">
            <h3 className="LogoLogin">Evansbook</h3>
            <span className="registerDesc">A site of kills and idears.</span>

          </div>



        <div className="registerRight">
          <div className="registerBox">

           

            
              <form action="" className="loginFormBox">

                <input type="text" placeholder="Username" id="username" className='registerInput' required />
                
                  <br />
                <input type="password" placeholder="Password" id="password" className='registerInput' required />


                <br />
               
                   <Link to={'/'}>
                      <button type='submit' className="loginRegisterButton">
                        Sign In
                      </button>
                    </Link>

                      <br />
                      <Link to={'/register'}>
                        <button className="loginSignUpButton">
                          Create new account
                        </button>
                      </Link>

               
                
                
              </form>
            

          </div>
        </div>





      </div>
    </div>
  );
};

export default Login;