import React from 'react'
import './register.scss'
import { DriveFileMoveOutlined } from '@mui/icons-material'
import { Link } from 'react-router-dom'
const Register = () => {
  return (
    <div className='register'>
      <div className="registerWrapper">
        <div className="registerLeft">
          <h3 className="registerLogo">Evansbook</h3>
          <span className="registerDesc">Connect with friends that have skills and idears around you on evansbook and all over the world.</span>

        </div>

        <div className="registerRight">
          <div className="registerBox">

            <div className="top">
              <img className='profileImg' src="/assets/profileCover/DefaultProfile.jpg" alt="" />

              <div className="formInput">
                <label htmlFor="file">Image: 
                  <DriveFileMoveOutlined className='icon' />
                  <input 
                  type="file" 
                  name="file" 
                  id="file" 
                  accept='.png,.jpeg,.jpg'
                  style={{display: 'none'}}
                   />
                </label>
              </div>
            </div>

            <div className="bottom">
              <form action="" className="bottomFormBox">

                <input type="text" placeholder="Username" id="username" className='registerInput' required />
                

                <input type="email" placeholder="Email" id="email" className='registerInput' required />

                <input type="password" placeholder="Password" id="password" className='registerInput' required />

                <input type="password" placeholder="Confirm password" id="confirmPassword" className='registerInput' required />

                
               <div className="displayFlex">

                    
                      <button type='submit' className="registerButton">
                        Sign Up
                      </button>
                    
                    
                    <Link to={'/login'} style={{textDecoration: 'none'}}>
                      <button className="logintoaccountbutton">
                        Into Account
                      </button>
                    </Link>
                    
               </div>
                
                
              </form>
            </div>

          </div>
        </div>

      </div>
    </div>
  )
}

export default Register