import React from 'react'
import './rightbarhome.scss'
import Online from '../online/Online'
import { Usersonline } from '../../data'
const Rightbarhome = () => {
  return (
      <div  className='rightbarhome'>
        <div className="birthdayContainer">

          <img src="/assets/birthdaygifts/gift.png" alt="" className="birhtdayImg" />
          <span className="birthdayText">
            <b>Ambar Dane</b> and <b>other friends</b>  have a birthday today
          </span>
        </div>
        <img src="/assets/ads/adv.jpg" alt="" className="rightbarAdvert" />
        <span className="rightbarTitle">
          Online Friends
        </span>

        <ul className="rightbarFriendList">
          {Usersonline.map((u) => (
            <Online key={u.id} Onlineuser={u}/>
          ))}
         
        </ul>
      </div>
    
  )
}

export default Rightbarhome