import React from 'react';
import SearchIcon from '@mui/icons-material/Search';
import PersonIcon from '@mui/icons-material/Person';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import './navbar.scss'
import { Link } from 'react-router-dom';

const Navbar = () => {
  return (
    <div className='navbarContainer'>

        <div className='navbarLeft'>
          <Link to='/' style={{textDecoration: 'none'}} >
            <span className='logo'>Evansbook</span>
          </Link>
          
        </div>

        <div className='navbarCenter'>
          
          <div className="searchBar">
              <SearchIcon className='searchIcon' />
              <input type="text" placeholder='Search for friend or videos' className='searchInput' />
          </div>

        </div>

        <div className='navbarRight'>
          

          <div className="navbarLinks">
            <span className="navbarLink">Homepage</span>

            <span className="navbarLink">Timeline</span>
          </div>



            <div className="navbarIcons">

              <div className="navbarIconItem">
                <PersonIcon/>
                <span className="navbarIconBadge">2</span>
              </div>

              <div className="navbarIconItem">
                <ChatBubbleOutlineIcon/>
                <span className="navbarIconBadge">10</span>
              </div>

              <div className="navbarIconItem">
                <NotificationsNoneIcon/>
                <span className="navbarIconBadge">2</span>
              </div>

            </div>

            <Link to={'/profile/userId'}>
              <img src="/assets/person/user.jpg" alt="person" className="navbarImg" />
            </Link>
            

          

        </div>

    </div>
  )
}

export default Navbar