import React, { useContext } from 'react'
import './sidebar.scss'
import MenuLink from "../menuLink/MenuLink";
import RssFeedIcon from '@mui/icons-material/RssFeed';
import ChatIcon from '@mui/icons-material/Chat';
import VideocamIcon from '@mui/icons-material/Videocam';
import PeopleIcon from '@mui/icons-material/People';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import StorefrontIcon from '@mui/icons-material/Storefront';
import EventIcon from '@mui/icons-material/Event';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import LogoutIcon from '@mui/icons-material/Logout';
import Friends from '../friends/Friends';
import { Users } from '../../data'
import { DarkModeContext } from './../../context/darkModeContext';
const Sidebar = () => {
  const {dispatch} = useContext(DarkModeContext);
  return (
    <div className='sidebar'>
      <div className="sidebarWrapper">
        <MenuLink Icon={<RssFeedIcon />} text="Feed"/>
        <MenuLink Icon={<ChatIcon />} text="Chat"/>
        <MenuLink Icon={<VideocamIcon />} text="Videos"/>
        <MenuLink Icon={<PeopleIcon />} text="Friends"/>
        <MenuLink Icon={<BookmarkIcon />} text="Bookmarks"/>
        <MenuLink Icon={<StorefrontIcon />} text="Market"/>
        <MenuLink Icon={<EventIcon />} text="Events"/>
        <span onClick={()=>dispatch({ type: "TOGGLE" })}>
          <MenuLink Icon={<DarkModeIcon />} text="Theme"/>
        </span>
        
        <MenuLink Icon={<LogoutIcon />} text="Logout"/>
        <button className='sidebarButton'>Show more</button>
        <hr className='sidebarHr' />
        <ul className="sidebarFriendList">

          {Users.map(u=> (
            <Friends key={u.id} Users={u} />
          ))}
          
          
        </ul>
      </div>
    </div>
  )
}

export default Sidebar