import React from 'react'
import './editprofile.scss'
import Navbar from '../../../components/navbar/Navbar'
import Sidebar from '../../../components/sidebar/Sidebar'
import { DriveFolderUploadOutlined } from '@mui/icons-material'



const Editprofile = () => {
  return (
    <div className='editprofile'>
      <Navbar />
      <div className="editprofileWrapper">
        <Sidebar />
        <div className="profileRight">
          <div className="profileRightTop">
            <div className="profileCover">
              <img src="/assets/profileCover/profilecover.jpg" alt="" className="profileCoverImg" />
              <img src="/assets/person/user.jpg" alt="" className="profileUserImg" />
            </div>
            <div className="profileInfo">
              <h4 className='profileInfoName'>Ambar Logan</h4>
              <span className="profileInfoDesc">Hi Friends!</span>
            </div>

            
          </div>

          <div className="editprofileRightButton">

            <div className="top">
              
              <h1 className='h1'>Edit Your Profile</h1>
            </div>

            <div className="bottom">
              <div className="left">
                <img className='img' src="/assets/profileCover/DefaultProfile.jpg" alt="" />
              </div>

              <div className="right">

                <form className='form'>
                  <div className="formInput">
                  <label htmlFor="file">
                    Image: <DriveFolderUploadOutlined className='Icon'/>
                  </label>
                  <input type="file" id="file" style={{display: 'none'}}/>
                </div>


                <div className="formInput">
                  <label>Name</label>
                  <input type="text" placeholder='Ambar Logan' />
                </div>

                <div className="formInput">
                  <label>UserName</label>
                  <input type="text" placeholder='Logan' />
                </div>

                <div className="formInput">
                  <label>Email</label>
                  <input type="email" placeholder='ambarlogan@gmail.com' />
                </div>

                <div className="formInput">
                  <label>Phone</label>
                  <input type="text" placeholder='+1 234 876 876' />
                </div>

                <div className="formInput">
                  <label>Adress</label>
                  <input type="text" placeholder='UN str' />
                </div>

                <div className="formInput">
                  <label>Country</label>
                  <input type="text" placeholder='U N' />
                </div>
                <button type='submit' className="updateButton">
                  Update Profile
                </button>
                </form>


                
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  )
}

export default Editprofile